
import XTable from 'vux/src/components/x-table/index.vue'

import { go } from 'vux/src/libs/router'

export default {
name:'m-table',
props:{
showMore: {
type: Boolean,
default: true
},
header:{
type: Object,
default () {
return {}
}
},
data: {
type: Array,
default () {
return []
}
},
moreLink: [String, Object]
},
methods:{
getHeader(item){
if(item==null || typeof(item)==="undefined")
return []
if (typeof(item)==="string")
return item
return item.display
},
getHeaderStyle(item){
if(item==null || typeof(item)==="undefined")
return []
if (typeof(item)==="string")
return []
return item.style
},
renderData(key,hItem,row,rIndex){
var value = row[key]
if(typeof hItem !== 'object')
return value
var render = hItem.render
if(typeof(render)==="function")
return render(value,key,row,rIndex)
return value
},
onClickMore(){


this.$emit('on-click-more',true);
}
},
components:{
XTable
},
computed:{
hWidth(){
var count = 0;
for (const key in this.header) {
if (this.header.hasOwnProperty(key)) {
count++
}
}
return (100/count)+'%'
}
}
}



