
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

export default {
directives: {
TransferDom
},
components: {
Popup,
Radio
},
props:{
data:{
type: Array,
default () {
return []
}
}
},
data(){
return {
showPop:false,

currFocusItem:{},
selectCon:{},
menuClick:false
}
},
methods:{
onMenuClick(item){
this.menuClick = true
this.currFocusItem = item;
this.showPop = !this.showPop;

this.$nextTick(x=>{
this.menuClick = false
})
},
getSelectCon(){
return easyClone(this.selectCon)
},
rChange(value,label){

if(this.menuClick)
return

this.selectCon[this.currFocusItem.key] = value

this.showPop = false
this.$emit('on-change',easyClone(this.selectCon))
},
getValue(item){

var op = item.options.filter(x => {
return x.key == this.selectCon[item.key]
})
if(op[0])
return op[0].value
return ''
}

},
computed:{
options(){
return this.currFocusItem.options||[]
}
},
created(){
this.data.forEach(x=>{
this.selectCon[x.key]=x.value
})

}
}
function easyClone(s){
var  t = {}
if(!s)
return t
for (const key in s) {
if (s.hasOwnProperty(key)) {
t[key] = s[key];
}
}
return t
}



